:root {
    --main-color: #e01d3f;
  }
  
  .Notifications {
    border: 3px dashed var(--main-color);
    padding: 10px;
    margin-bottom: 20px;
  }
  
  .Notifications button img {
    width: 10px;
  }
  
  .Notifications p {
    margin: 0;
    margin-top: 15px;
  }
  
  [data-priority="default"] {
    color: blue;
  }
  
  [data-priority="urgent"] {
    color: red;
  }